'use client';
import React, { useState } from "react"; 
import { Navbar, Nav, Container, Row, Col, NavDropdown, Button } from "react-bootstrap";
import Logo from "../Images/newlogo.png";
import './style.css';
import { FaFacebookF, FaTwitter, FaInstagram, FaLinkedinIn, FaComments } from 'react-icons/fa';

function Header() {
  const [expanded, setExpanded] = useState(false); // üî• Navbar open/close state

  const handleNavClick = () => {
    setExpanded(false); // üî• Auto close on menu click
  };

  return (
    <>
      <div className='top-header' style={{ backgroundColor: '#5458f7', color: 'white', padding: '8px 0', fontSize: '15px' }}>
        <Container>
          <Row className='align-items-center'>
            <Col className='d-flex align-items-center'>
              <span className='me-3'>üìß info@yourcompany.com</span>
              <span className='me-3'>üìû +92 300 000 0000</span>
              <span className='d-flex align-items-center'><FaComments /> Chat</span>
            </Col>
            <Col className='d-flex justify-content-end gap-3'>
              <a href='#' style={{ color: 'white' }}><FaFacebookF /></a>
              <a href='#' style={{ color: 'white' }}><FaTwitter /></a>
              <a href='#' style={{ color: 'white' }}><FaInstagram /></a>
              <a href='#' style={{ color: 'white' }}><FaLinkedinIn /></a>
            </Col>
          </Row>
        </Container>
      </div>

      <Navbar expand="lg" expanded={expanded} onToggle={setExpanded} className="py-3 shadow-sm bg-white">
        <Container fluid>
          <Navbar.Brand href="/">
            <img
              src={Logo.src}
              alt="Logo"
              width="100"
              className="img-fluid"
            />
          </Navbar.Brand>

          {/* Toggle controls 'expanded' state */}
          <Navbar.Toggle aria-controls="main-navbar" />

          <Navbar.Collapse id="main-navbar">
            <Nav className="mx-auto">

              <Nav.Link href="/" onClick={handleNavClick}>Home</Nav.Link>

              <NavDropdown title="Services" id="mega-menu">
                <div className="p-4" style={{ minWidth: "800px" }}>
                  <Row>
                    <Col md={6}>
                      <h6 className="fw-bold">Textile</h6>
                      <NavDropdown.Item href="/tennis-clothing-manufacturer" onClick={handleNavClick}>Tennis Clothing Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/golf-apparel-manufacturer" onClick={handleNavClick}>Golf Apparel Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/sweat-suit-manufacturer" onClick={handleNavClick}>Sweat Suit Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/gym-clothing-manufacturer" onClick={handleNavClick}>Gym Clothing Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Cycling-clothing-manufacturers" onClick={handleNavClick}>Cycling Clothing Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Gymnastics-leotard-manufacturers" onClick={handleNavClick}>Gymnastics Leotard Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-jackets-manufacturer" onClick={handleNavClick}>Custom Jackets Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-shorts-manufacturer" onClick={handleNavClick}>Custom Shorts Manufacturer</NavDropdown.Item>
                    </Col>
                    <Col md={6}>
                      <h6 className="fw-bold">Apparel</h6>
                      <NavDropdown.Item href="/Yoga-wear-manufacturer" onClick={handleNavClick}>Yoga Wear Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Hunting-clothing-manufacturer" onClick={handleNavClick}>Hunting Clothing Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-tracksuit-manufacturers" onClick={handleNavClick}>Custom Tracksuit Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Baseball-clothing-manufacturers" onClick={handleNavClick}>Baseball Clothing Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-hoodie-manufacturer" onClick={handleNavClick}>Custom Hoodie Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-underwear-manufacturer" onClick={handleNavClick}>Custom Underwear Manufacturer</NavDropdown.Item>
                      <NavDropdown.Item href="/Custom-pajama-manufacturers" onClick={handleNavClick}>Custom Pajama Manufacturers</NavDropdown.Item>
                      <NavDropdown.Item href="/Fishing-shirt-manufacturers" onClick={handleNavClick}>Fishing Shirts Manufacturing</NavDropdown.Item>
                    </Col>
                  </Row>
                </div>
              </NavDropdown>

              <Nav.Link href="/portfolio" onClick={handleNavClick}>Portfolio</Nav.Link>
              <Nav.Link href="/about-us" onClick={handleNavClick}>About</Nav.Link>
              <Nav.Link href="/services" onClick={handleNavClick}>Blogs</Nav.Link>
              <Nav.Link href="/contact-us" onClick={handleNavClick}>Contact</Nav.Link>
            </Nav>

            <div className="ms-auto">
              <Button variant="home-btn-primary" onClick={() => setExpanded(false)}>
                GET STARTED
              </Button>
            </div>

          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;
